import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/tsla',
      name: 'TSLA',
      component: _ => import('./TSLA')
    }, {
      path: '/nvda',
      name: 'NVDA',
      component: _ => import('./NVDA')
    }
  ]
})



